<template>
  <div class="main-wrapper">
    <!-- ============================================================== -->
    <!-- Preloader - style you can find in spinners.css -->
    <!-- ============================================================== -->

    <!-- ============================================================== -->
    <!-- Preloader - style you can find in spinners.css -->
    <!-- ============================================================== -->
    <!-- ============================================================== -->
    <!-- Login box.scss -->
    <!-- ============================================================== -->
    <div class="auth-wrapper d-flex no-block justify-content-center align-items-center position-relative"
         style="background:url(static/images/big/auth-bg.jpg) no-repeat center center;">
      <div class="auth-box row" style="height: 510px">
        <div class="col-lg-7 col-md-5 modal-bg-img" style="background-image: url(static/images/big/3.jpg);">
        </div>
        <div class="col-lg-5 col-md-7 bg-white">
          <div class="p-3">
            <div class="text-center">
              <img src="static/images/big/icon.png" alt="wrapkit">
            </div>
            <h2 class="mt-3 text-center">Sign In</h2>
            <p class="text-center">输入您的用户名和密码以访问管理面板.</p>
            <form action="#" method="post" class="mt-4">
              <div class="row" style="height: 300px;">
                <div class="col-lg-12">
                  <div class="form-group">

                    <label class="text-dark" for="uname">Username</label>
                    <input v-model="username" class="form-control" id="uname" type="text"
                           placeholder="请输入你用户名">
                  </div>
                </div>
                <div class="col-lg-12">
                  <div class="form-group">
                    <label class="text-dark" for="pwd">Password</label>
                    <input v-model="password" class="form-control" id="pwd" type="password"
                           placeholder="请输入你的密码">
                  </div>
                </div>
                <div class="col-lg-12 text-center">
                  <div type="submit" @click="loginhander" style="line-height: 32px;" class="btn btn-block btn-dark">Sign
                    In
                  </div>
                </div>
                <div class="col-lg-12 text-center mt-5">
                  还没有账号？ <a href="#" class="text-danger">联系管理员</a>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
    <!-- ============================================================== -->
    <!-- Login box.scss -->
    <!-- ============================================================== -->
  </div>
</template>

<script>
import {fetchData} from '@/api/index';

export default {
  name: "Login",
  data() {
    return {
      username: '',
      password: '',
      title: 'Home组件'
    }
  },
  methods: {
    loginhander() {


      let data_send = {
        username: this.username,
        password: this.password,
      }
      // axios请求在这里
      fetchData(
        {data: data_send}
      )
        .then((data) => {

          if (data.data.token){
                 localStorage.token = data.data.token;
          }else {
                      this.$alert('服务端未获取到数据,请联系管理员！', '登录失败', {
            confirmButtonText: '确定',
          });
                      return
          }

          localStorage.username = data.data.username;
          if(data.data.avatar){
              localStorage.avatar = this.$settings.Host+ '/static/'+ data.data.avatar;
          }


          this.$router.push('/');
        })
        .catch((err) => {

          this.$alert('用户名或者密码错误', '登录失败', {
            confirmButtonText: '确定',

          });
        });


    },

  }
}

</script>
